      grid-template-columns: repeat(3, minmax(0, 1fr));
    }
  }
}
:root {
  --font-heading: 'Space Grotesk', -apple-system, BlinkMacSystemFont, system-ui, sans-serif;
  --font-ui: 'Space Grotesk', -apple-system, BlinkMacSystemFont, system-ui, sans-serif;
  --font-mono: 'JetBrains Mono', 'Fira Code', ui-monospace, SFMono-Regular, monospace;
  --ide-canvas-bg: #1a1a1a;
  --ide-canvas-surface: #242424;
  --ide-canvas-grid: #2d2d2d;
  --ide-panel-bg: #0d0d0d;
  --ide-panel-surface: #161616;
  --ide-panel-hover: #1f1f1f;
  --ide-border: #2a2a2a;
  --ide-border-subtle: #222222;
  --ide-border-focus: #404040;
  --ide-text: #e5e5e5;
  --ide-text-muted: #a3a3a3;
  --ide-text-subtle: #737373;
  --ide-accent: #f59e0b;
  --ide-accent-hover: #fbbf24;
  --ide-accent-dim: #b45309;
  --ide-accent-glow: rgba(245, 158, 11, 0.25);
  --ide-success: #22c55e;
  --ide-success-dim: #166534;
  --ide-error: #ef4444;
  --ide-error-dim: #991b1b;
  --ide-warning: #eab308;
  --ide-info: #3b82f6;
  --pro-bg: #FAFAFA;
  --pro-bg-subtle: #F4F4F5;
  --pro-surface: #FFFFFF;
  --pro-border: #E4E4E7;
  --pro-border-subtle: #F4F4F5;
  --pro-text: #18181B;
  --pro-text-muted: #71717A;
  --pro-text-subtle: #A1A1AA;
  --pro-accent: #F59E0B;
  --pro-accent-hover: #D97706;
  --pro-success: #10B981;
  --pro-error: #EF4444;
  --background: var(--ide-panel-bg);
  --foreground: var(--ide-text);
  --brass: #F59E0B;
  --brass-dim: #D97706;
  --electric: #F59E0B;
  --void: #0d0d0d;
  --panel: #161616;
}
body {
  background: var(--ide-panel-bg);
  color: var(--ide-text);
  color-scheme: dark;
  font-family: var(--font-ui), -apple-system, BlinkMacSystemFont, system-ui, sans-serif;
  overflow: hidden;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}
.glass-panel {
  backdrop-filter: blur(16px) saturate(150%);
  -webkit-backdrop-filter: blur(16px) saturate(150%);
  background-color: rgba(22, 22, 22, 0.85);
  border: 1px solid var(--ide-border);
  box-shadow: 0 4px 24px rgba(0, 0, 0, 0.4),
    inset 0 1px 0 rgba(255, 255, 255, 0.03);
}
.glass-panel-heavy {
  backdrop-filter: blur(24px) saturate(180%);
  -webkit-backdrop-filter: blur(24px) saturate(180%);
  background-color: rgba(13, 13, 13, 0.95);
  border: 1px solid var(--ide-border);
  box-shadow: 0 8px 32px rgba(0, 0, 0, 0.5),
    inset 0 1px 0 rgba(255, 255, 255, 0.04);
}
.ide-panel {
  background: var(--ide-panel-surface);
  border: 1px solid var(--ide-border);
}
.ide-panel-bg {
  background: var(--ide-panel-bg);
}
.ide-canvas {
  background: var(--ide-canvas-bg);
  background-image: radial-gradient(circle at 1px 1px, var(--ide-canvas-grid) 1px, transparent 0);
  background-size: 20px 20px;
}
.pro-card {
  background: var(--ide-panel-surface);
  border: 1px solid var(--ide-border);
  border-radius: 8px;
  box-shadow: 0 2px 8px rgba(0, 0, 0, 0.3);
}
.floating-bar {
  background: rgba(22, 22, 22, 0.9);
  backdrop-filter: blur(20px) saturate(180%);
  -webkit-backdrop-filter: blur(20px) saturate(180%);
  border: 1px solid var(--ide-border);
  border-radius: 12px;
  box-shadow: 0 8px 32px rgba(0, 0, 0, 0.5),
    inset 0 1px 0 rgba(255, 255, 255, 0.05);
}
.simulation-active {
  box-shadow: 0 0 0 2px rgba(34, 197, 94, 0.3),
    0 0 30px rgba(34, 197, 94, 0.2);
  animation: sim-glow 2s ease-in-out infinite;
}
@keyframes sim-glow {
  0%, 100% {
    box-shadow: 0 0 0 2px rgba(34, 197, 94, 0.3),
      0 0 20px rgba(34, 197, 94, 0.15);
  }
  50% {
    box-shadow: 0 0 0 3px rgba(34, 197, 94, 0.4),
      0 0 40px rgba(34, 197, 94, 0.25);
  }
}
.btn-press {
  transition: transform 0.1s ease, box-shadow 0.1s ease;
}
.btn-press:active {
  transform: scale(0.97);
}
.accent-glow {
  box-shadow: 0 0 0 3px var(--ide-accent-glow);
}
.glow-ring-active {
  animation: glow-ring 2s ease-in-out infinite;
}
@keyframes glow-ring {
  0%, 100% {
    box-shadow: 0 0 0 0 rgba(245, 158, 11, 0.4);
  }
  50% {
    box-shadow: 0 0 0 6px rgba(245, 158, 11, 0.15);
  }
}
.mic-recording {
  animation: mic-pulse 1.5s ease-in-out infinite;
}
@keyframes mic-pulse {
  0%, 100% {
    box-shadow: 0 0 0 0 rgba(239, 68, 68, 0.6);
    background-color: rgba(239, 68, 68, 0.2);
  }
  50% {
    box-shadow: 0 0 0 8px rgba(239, 68, 68, 0);
    background-color: rgba(239, 68, 68, 0.3);
  }
}
.toast-enter {
  animation: toast-slide-in 0.3s ease-out forwards;
}
.toast-exit {
  animation: toast-slide-out 0.2s ease-in forwards;
}
@keyframes toast-slide-in {
  from {
    opacity: 0;
    transform: translateX(100%);
  }
  to {
    opacity: 1;
    transform: translateX(0);
  }
}
@keyframes toast-slide-out {
  from {
    opacity: 1;
    transform: translateX(0);
  }
  to {
    opacity: 0;
    transform: translateX(100%);
  }
}
.skeleton {
  background: linear-gradient(
    90deg,
    var(--ide-panel-surface) 25%,
    var(--ide-panel-hover) 50%,
    var(--ide-panel-surface) 75%
  );
  background-size: 200% 100%;
  animation: skeleton-shimmer 1.5s ease-in-out infinite;
}
@keyframes skeleton-shimmer {
  0% {
    background-position: 200% 0;
  }
  100% {
    background-position: -200% 0;
  }
}
.resize-handle {
  background: transparent;
  transition: background-color 0.2s ease;
}
.resize-handle:hover {
  background: var(--ide-accent-glow);
}
.resize-handle:active {
  background: var(--ide-accent);
}
::-webkit-scrollbar {
  width: 8px;
  height: 8px;
}
::-webkit-scrollbar-track {
  background: var(--ide-panel-bg);
  border-radius: 4px;
}
::-webkit-scrollbar-thumb {
  background: var(--ide-border);
  border-radius: 4px;
}
::-webkit-scrollbar-thumb:hover {
  background: var(--ide-border-focus);
}
.react-flow__controls {
  border: 1px solid var(--ide-border);
  border-radius: 8px;
  overflow: hidden;
  background: rgba(22, 22, 22, 0.9);
  backdrop-filter: blur(12px);
  box-shadow: 0 4px 16px rgba(0, 0, 0, 0.4);
}
.react-flow__controls button {
  width: 32px;
  height: 32px;
  border: 0;
  border-bottom: 1px solid var(--ide-border);
  background: transparent;
  color: var(--ide-text-muted);
  transition: background-color 150ms ease, color 150ms ease;
}
.react-flow__controls button:last-child {
  border-bottom: none;
}
.react-flow__controls button:hover {
  background: var(--ide-panel-hover);
  color: var(--ide-text);
}
.react-flow__controls button svg {
  width: 14px;
  height: 14px;
}
.react-flow__controls button:focus-visible {
  outline: 2px solid var(--ide-accent);
  outline-offset: -2px;
}
.react-flow__background {
  background: var(--ide-canvas-bg) !important;
}
.react-flow__selection {
  background: rgba(245, 158, 11, 0.1);
  border: 1px solid var(--ide-accent);
}
.react-flow__edge-path {
  stroke: var(--ide-text-muted);
  stroke-width: 2;
}
.react-flow__edge.selected .react-flow__edge-path {
  stroke: var(--ide-accent);
}
.animate-in {
  animation: fade-in 0.3s ease-out, slide-up 0.4s ease-out;
}
@keyframes fade-in {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}
@keyframes slide-up {
  from {
    opacity: 0;
    transform: translateY(8px);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}
